import {defineComponent, PropType} from "vue";
import styles from './index.module.scss'
import {
	FlexAlignContent,
	FlexAlignItems,
	FlexAlignSelf,
	FlexDirection,
	FlexJustifyContent
} from "@/components/Flex/types";
import {turnHump} from "@/utils";
import {onClick} from "@/types";

export default defineComponent({
	name: 'FlexBox',
	props: {
		direction: {
			type: String as PropType<FlexDirection>,
			default: FlexDirection.row
		},
		wrap: {
			type: Boolean as PropType<boolean>,
			default: false
		},
		inline: {
			type: Boolean as PropType<boolean>,
			default: false
		},
		justifyContent: {
			type: String as PropType<FlexJustifyContent>,
			default: FlexJustifyContent["flex-start"]
		},
		alignContent: {
			type: String as PropType<FlexAlignContent>,
			default: FlexAlignContent.stretch
		},
		alignItems: {
			type: String as PropType<FlexAlignItems>,
			default: FlexAlignItems.stretch
		},
		onClick: {
			type: Function as PropType<onClick<MouseEvent>>
		}
		// ...PropsType
	},
	setup(props, {slots}) {
		const clickEventHandler = (e: MouseEvent) => {
			props.onClick?.(e)
		}
		const classes: Array<string | object> = [
			'flex',
			styles.flex,
			styles[props.direction],
			styles[props.justifyContent],
			styles[props.alignContent],
			styles[props.alignItems],
			{
				wrap: props.wrap,
				inline: props.inline,
				// [props.class]: !!props.class
			}
		]
		return () => <section class={classes} onClick={clickEventHandler}>
			{slots.default?.()}
		</section>
	}
})

