// 	shrink: PropType<number> | 1,
// 	grow: PropType<number> | 0,
// 	class: PropType<string[] | object[] | string | undefined>
// }

export const Item = defineComponent({
	name: 'FlexItem',
	props: {
		alignSelf: {
			type: String as PropType<FlexAlignSelf>,
			default: FlexAlignSelf.auto
		},
		shrink: {
			type: Number as PropType<number>,
			default: 1
		},
		grow: {
			type: Number as PropType<number>,
			default: 0
		},
		onClick: {
			type: Function as PropType<onClick<MouseEvent>>
		}
		// ...PropsType
	},
	setup(props, {slots}) {
		const classes: Array<string | object> = [
			'flex-item',
			styles.flexItem,
			styles[`itemShrink${props.shrink}`],
			styles[`itemGrow${props.grow}`],
			styles[`alignSelf${turnHump(props.alignSelf)}`]
		]
		const clickEventHandler = (e: MouseEvent) => {
			props.onClick?.(e)
		}
		return () => <section class={classes} onClick={clickEventHandler}>
			{slots.default?.()}
		</section>
	}
})