import {defineComponent, reactive, ref} from "vue";
import styles from './index.module.scss'
import title from './images/title.png'
import phone from './images/phone.png'
import verificationCode from './images/verification_code.png'
import Flex, {Item} from "@/components/Flex";
import {FlexAlignItems, FlexJustifyContent} from "@/components/Flex/types";
import Button from "@/components/Button";
import {ButtonTypes} from "@/components/Button/types";
import {getVerificationCode} from "@/models/commonModels";
import {useToast} from "vue-toastification";
import {useMapActions} from "@/hooks/store";
import {useRouter} from "vue-router";
import SvgIcon from "@/plugins/SvgIcon/SvgIcon";

interface LoginParams {
	number: string,
	code: string
}

const DEFAULT_CODE_BTN_TEXT = `获取验证码`
export default defineComponent({
	name: 'LoginForm',
	setup() {
		const account = reactive<LoginParams>({
			number: process.env.VUE_APP_PHONE,
			code: process.env.VUE_APP_CODE
		})
		const {
			login, queryUser
		} = useMapActions({
			actions: ['login', 'queryUser']
		})
		const toast = useToast()
		const router = useRouter()
		const timer = ref<number | null>()
		const codeBtnText = ref<string>(DEFAULT_CODE_BTN_TEXT)
		// 请求获取验证码
		const codeLoading = ref<boolean>(false)
		// 锁定获取验证码函数
		const codeDisabled = ref<boolean>(false)
		// 同意协议框
		const agreement = ref<boolean>(false)
		// 请求登录
		const loginLoading = ref<boolean>(false)
		// 倒计时
		const countdown = (ttl: number): void => {
			if (!ttl) {
				codeBtnText.value = DEFAULT_CODE_BTN_TEXT
				codeDisabled.value = false
				return;
			}
			timer.value = setTimeout(() => {
				codeBtnText.value = `${--ttl}s`
				countdown(ttl)
			}, 1000)
		}
		// 获取验证码
		const codeClickEventHandler = (): void => {
			codeLoading.value = true
			getVerificationCode({number: account.number})
				.then(({result}) => {
					codeDisabled.value = true
					countdown(result.ttl)
				})
				.catch(err => {
					console.error(err)
				})
				.finally(() => {
					codeLoading.value = false
				})
		}
		// 切换同意协议
		const clickAgreementHandler = (): void => {
			agreement.value = !agreement.value
		}
		// 登录事件
		// eslint-disable-next-line
		const clickLoginHandler = async (): Promise<any> => {
			if (!/^1[\d]{10}$/.test(account.number)) {
				toast('请输入11位手机号')
				return;
			} else if (!/^[\d]{6}$/.test(account.code)) {
				toast('请输入验证码')
				return;
			}
			loginLoading.value = true
			try {
				await login(account)
				await queryUser()
				router.replace('/')
				// eslint-disable-next-line
			} catch (err: any) {
				console.error(err)
				toast(err?.msg)
			} finally {
				loginLoading.value = false
			}
		}

		return () => (
			<main class={styles.form}>
				<h1 class={styles.formTitle}>
					<img src={title} alt={"万峻用户中心"} />
				</h1>
				<section class={styles.formBody}>
					<Flex class={styles.formItem} alignItems={FlexAlignItems.center}>
						<Item class={styles.formItemIcon} shrink={0}>
							<img src={phone} alt={"手机号"} />
						</Item>
						<Item grow={1}>
							<input placeholder={"请输入手机号"} v-model={account.number} maxlength={11} />
						</Item>
					</Flex>
					<Flex class={styles.formItem} alignItems={FlexAlignItems.center}>
						<Item class={styles.formItemIcon} shrink={0}>
							<img src={verificationCode} alt={"验证码"} />
						</Item>
						<Item class={styles.formItemCode}>
							<input placeholder={"请输入验证码"} v-model={account.code} />
						</Item>
						<Item grow={1}>
							<Button type={ButtonTypes.info} loading={codeLoading.value} disabled={codeDisabled.value}
								class={styles.formItemCodeBtn}
								onClick={codeClickEventHandler}>{codeBtnText.value}</Button>
						</Item>
					</Flex>
					<Flex
						class={[
							styles.formItem, styles.formItemText
						]}
						alignItems={FlexAlignItems.center}
						justifyContent={FlexJustifyContent.center}
					>
						<Item>
							<div
								class={[
									styles.formItemTextCheckbox, {[styles.formItemTextCheckboxActive]: agreement.value}
								]}
								onClick={clickAgreementHandler}
							>
								{agreement.value ? <SvgIcon width={14} height={14} name={"tick"} /> : null}
							</div>
						</Item>
						<Item>
							<div>勾选并同意 <a>《xxxxxxx服务协议》</a></div>
						</Item>
					</Flex>
					<Flex
						class={[
							styles.formItem, styles.formItemText, styles.formItemTextLoginBtn
						]}
					>
						<Item grow={1}>
							<Button loading={loginLoading.value} disabled={loginLoading.value || !agreement.value}
								type={ButtonTypes.warning} style={{height: "100%"}}
								onClick={clickLoginHandler}>登录</Button>
						</Item>
					</Flex>
				</section>
			</main>
		)
	}
})