import {defineComponent, PropType} from "vue";
import styles from './button.module.scss'
import {ButtonTypes} from "@/components/Button/types";
import Loading from "@/components/Loading";
import {turnHump} from "@/utils";
// interface ButtonProps {
// 	type?: 'default' | 'primary' | 'info' | 'warning' | 'danger';
// 	loading?: true | false;
// }
console.log(styles)
export default defineComponent({
	name: 'ButtonComponent',
	props: {
		type: {
			type: String as PropType<ButtonTypes>,
			default: ButtonTypes.default
		},
		loading: {
			type: Boolean as PropType<boolean>,
			default: false
		},
		disabled: {
			type: Boolean as PropType<boolean>,
			default: false
		},
		onClick: {
			// eslint-disable-next-line
			type: Function as PropType<(e: MouseEvent) => any>,
		},
		plain: {
			type: Boolean as PropType<boolean>,
			default: false
		}
	},
	setup(props/*: ButtonProps*/, {slots}) {
		const classes: Array<string | object> = [
			styles.mtBtn,
			styles[`mtBtn${turnHump(props.type)}`],
			{
				[styles.mtBtnPlain]: props.plain
			}
			// styles.mtBtnInfo,
			// styles.mtBtnPrimary,
			// styles.mtBtnDisabled,
			// styles.mtBtnLoading
		]
		// 事件处理
		const clickEventHandler = (e: MouseEvent): void => props.onClick?.(e)
		return () => <button onClick={clickEventHandler} disabled={props.loading || props.disabled} class={classes}>
			{props.loading ? <Loading /> : null}
			{slots.default ? slots.default() : 'button'}
		</button>
	}
})