/**
 * 转为驼峰
 * @param str
 */
export const turnHump = (str: string): string => {
	const arr: Array<string> = str.split('-')
	const temps: string[] = []
	for (let i = 0, len = arr.length; i < len; i++) {
		const [first, ...temp] = arr[i]
		temps.push(first.toUpperCase(), ...temp)
	}
	return temps.join('')
}

/**
 * 隐藏手机号中间四位
 * @param phone
 */
export const hidePhoneNumber = (phone: string): string | undefined => {
	if (!phone) return undefined;
	return phone.replace(/^(\d{3})\d{4}(\d{4})$/, "$1****$2")
}
